export const useAuthors = (authors: string) => {
  const REMOVE_STR = '()';
  const authorArr = authors.split(',');
  const len = authorArr.length;
  if (len === 1) {
    const author = authors.replace(REMOVE_STR, '');
    return author;
  } else {
    const author = authorArr[0].replace(REMOVE_STR, '');
    return `${author}  ${len - 1}`;
  }
};
